import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);
class FormModalTimbangRosok extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="col-lg-12">
          <div className="row">
            <div className="col-lg-4">
              <label htmlFor="">Kode barang</label>
              <Field
                name="kodeBarang"
                className="form-control"
                component="select"
              >
                <option value="DEFAULT" key="DEFAULT">
                  DEFAULT
                </option>
              </Field>
            </div>
            <div className="col-lg-3">
              <Field
                name="kadar"
                label="Kadar(%)"
                type="text"
                component={renderField}
              />
            </div>
            <div className="col-lg-3">
              <Field
                name="bruto"
                label="Bruto(Gr)"
                type="text"
                component={renderField}
              />
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormModalTimbangRosok = reduxForm({
  form: "FormModalTimbangRosok",
  enableReinitialize: true,
})(FormModalTimbangRosok);
export default connect()(FormModalTimbangRosok);
