import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import paginationFactory from "react-bootstrap-table2-paginator";
import FormModalTimbangRosok from "./Components/FormModalTimbangRosok";

const { SearchBar } = Search;
class TotalTimbangRosok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "kode",
          text: "Kode",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "harga",
          text: "Harga",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "kadar",
          text: "Kadar",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "gross",
          text: "Gross (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "pkg",
          text: "Pkg (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "berat",
          text: "Berat (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "harga",
          text: "Harga",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "netto",
          text: "Netto (Gr)",
          sort: true,
          headerStyle: () => {
            return { width: "10%" };
          },
        },
        {
          dataField: "ongkos",
          text: "Ongkos (Rp)",
          sort: true,
          headerStyle: () => {
            return { width: "25%" };
          },
        },
      ],
      data: [
        {
          tanggal: "2020-11-10",
        },
      ],
    };
  }
  render() {
    return (
      <div className="container-fluid">
        <div className="card">
          <div className="card-body">
            <div className="row">
              <div className="col-lg-4">
                <div className="form-group">
                  <label htmlFor="">Tanggal</label>
                  <input
                    type="date"
                    name="tanggal"
                    label="tanggal"
                    className="form-control"
                  />
                </div>
              </div>
              <div className="col-lg-12">
                <div className="text-right">
                  <button
                    className="btn btn-primary"
                    data-target="#tambahModal"
                    data-toggle="modal"
                  >
                    Tambah Data
                  </button>
                </div>
              </div>
              <div className="col-lg-12">
                <ToolkitProvider
                  keyField="kodeKelompok"
                  data={this.state.data}
                  columns={this.state.columns}
                  search
                >
                  {(props) => (
                    <div className="form-group">
                      <div className="text-right mt-5 mb-3">
                        <SearchBar
                          {...props.searchProps}
                          className="form-control"
                          placeholder="Tuliskan Kode / Nama"
                          style={{ margin: 0, top: "50%" }}
                        />
                      </div>
                      <BootstrapTable
                        {...props.baseProps}
                        striped
                        hover
                        condensed
                        bordered={true}
                        headerClasses="header-table"
                        pagination={paginationFactory()}
                      />
                    </div>
                  )}
                </ToolkitProvider>
              </div>
            </div>
          </div>
        </div>
        <div
          id="tambahModal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Timbang Rongsok
                </h4>
                <button
                  id="dismiss_modal"
                  name="dismiss_modal"
                  type="button"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  ×
                </button>
              </div>
              <div className="modal-body">
                <FormModalTimbangRosok />
              </div>
              <div className="modal-footer">
                <button
                  type="button"
                  className="btn btn-light"
                  data-dismiss="modal"
                  id="close_modal"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default TotalTimbangRosok;
